<template>
  <BaseLayout nonavbar>

    <van-sticky :offset-top="0">
      <van-row class="toolbar" gutter="0" justify="space-around" align="center">
        <van-col span="2">
          <van-icon name="arrow-left" color="#000" size="large" @click="$router.back()"></van-icon>
        </van-col>
        <van-col span="2">
          <van-icon name="cluster-o" color="#000" size="large" :style="{transform:'rotate(270deg)'}" @click="showCategory"></van-icon>
        </van-col>
        <van-col span="17">
          <van-search v-model="state.keyword" placeholder="请输入搜索关键词" background="transparent" input-align="left" @search="refresh" />
        </van-col>
        <van-col span="2">
          <van-icon :name="showGrid ? 'bars' : 'apps-o'" size="large" @click="toggleGrid"></van-icon>
        </van-col>
      </van-row>
      <van-dropdown-menu>
        <van-dropdown-item v-model="state.order" :options="state.options" @change="refresh" />
        <van-dropdown-item title="筛选" ref="item">
          <van-cell center title="价格区间">
            <input type="number" v-model="state.min" :style="{width: '60px'}"/>
              ~
            <input type="number" v-model="state.max" :style="{width: '60px'}"/>
          </van-cell>
          <van-cell center title="推荐商品">
            <template #right-icon>
              <van-switch v-model="state.recommend" size="24" active-color="#ee0a24" />
            </template>
          </van-cell>
          <div style="padding: 5px 16px;">
            <van-button type="danger" block round @click="refresh">确认</van-button>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </van-sticky>

    <van-pull-refresh v-model="state.refreshing" loading-text=" " @refresh="refresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="query">
        <mall-item :grid="showGrid" :item="item" v-for="(item, index) in state.list" :key="index" @add="addCart"></mall-item>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

    <van-popup v-model:show="state.popup" closeable position="left" :style="{width:'250px', height:'100%', paddingTop: '5px'}">
      <van-cell :title="isTop ? '全部商品' : '上级分类'" icon="arrow-left" clickable @click="isTop ? changeCategory({cid:0}) : changeCategory()" />
      <van-tree-select v-model:main-active-index="state.active" :style="{height: '100%'}" :items="state.category" @click-nav="changeCategory" @click-item="changeCategory"/>
    </van-popup>

    <mall-item-chooser :id="state.id" :show="$store.state.showPopup" @save="saveCart"></mall-item-chooser>

  </BaseLayout>
</template>

<style scoped>
.toolbar {
  background: #FFF url('~@/assets/bg_navbar.jpg') repeat left center;
  text-align:center;
}
.list {
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  justify-content: space-evenly;
  min-height: 500px;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'
import MallItem from '@/components/MallItem.vue'
import MallItemChooser from '@/components/MallItemChooser.vue'

export default {
  components: {BaseLayout, MallItem, MallItemChooser},

  props: {
    type: {
      type: String,
      default: "mall",
    },
    cid: {
      type: [String, Number],
      default: 0,
    },
    keyword: {
      type: String,
      default: "",
    },
  },

  data() {
    const state = reactive({
      // 查询
      keyword: "",
      order: 0,
      options: [
        { text: '默认排序', value: 0 },
        { text: '销量', value: 1 },
        { text: '价格由低到高', value: 2 },
        { text: '价格由高到低', value: 3 },
      ],
      recommend: 0,
      min: "",
      max: "",
      offset: 0,
      limit: 10,

      // 列表
      refreshing: false,
      loading: false,
      finished: false,
      list: [],

      // 弹出类目
      popup: false,
      category: [],
      active: 0,
      pid: 0,

      // 弹出商品选择
      id: 0,
    });

    return {state};
  },

  mounted() {
    this.state.keyword = this.keyword;
    this.refresh();
  },

  computed: {
    isTop() {
      return !this.state.category[parseInt(this.state.active)] || this.state.category[parseInt(this.state.active)].pid == 0;
    },
    ...mapState(["showGrid"]),
  },

  methods: {
    
    refresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.query();
    },

    query() {
      this.$http.get("/mall/itemlist?cid=" + this.cid + "&keyword=" + this.state.keyword + "&order=" + this.state.order + "&recommend=" + (this.state.recommend ? 1 : 0) + "&max=" + this.state.max + "&min=" + this.state.min + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        this.state.list = this.state.list.concat(data);
      });
    },

    showCategory() {
      let mid = 0;
      for (let i in this.$store.state.channel){
        if (this.$store.state.channel[i].type == this.type){
          mid = parseInt(i);
        }
      }
      this.$http.post("/mall/category?mid=" + mid + "&id=" + this.cid).then((data) => {
        this.state.category = data;
        for (let i in data){
          let id = parseInt(this.cid);
          if (data[i].cid == id)
            this.state.active = i;
        }
        this.state.popup = true;
      });
    },
    async changeCategory(o){
      this.state.popup = false;
      let cid = 0;
      if (typeof o == "undefined") {
        cid = this.state.category[parseInt(this.state.active)].pid;
      }
      else if (typeof o == "number") {
        cid = this.state.category[parseInt(this.state.active)].cid;
      }
      else if (typeof o == "object") {
        cid = o.cid;
      }
      await this.$router.replace("list?cid=" + cid);
      this.refresh();
    },

    addCart(obj){
      this.state.id = obj.id;
      this.togglePopup(true);
    },
    saveCart(obj){
      let status = parseInt(obj.digital) == 0 ? 1 : 2;
      this.$http.post("/mall/cartsave?spu=" + obj.id + "&sku=" + obj.sku + "&num=" + obj.num + "&status=" + status).then((data) => {
        if (status == 1)
          this.cartNumberAdd(obj.num);
        else
          this.$router.push("bill?direct=1&digital=" + obj.digital);
      });
    },

    ...mapMutations(["togglePopup","toggleTabbar","toggleGrid","cartNumberAdd"]),
  }
}
</script>
